import { dedupingMixin } from '@bbva-web-components/bbva-core-lit-helpers/utils/mixin.js';

/**
 * Symbol for the `elementsWithTransitionsAndAnimations` property.
 *
 * [BbvaCoreTransitionsAndAnimationsEffectMixin](BbvaCoreTransitionsAndAnimationsEffectMixin) inspects this property
 * to determine which element(s) have CSS transitions/animations applied to them for visual effects.
 *
 * @returns {Element[]} The elements with CSS transitions
 */
export const elementsWithTransitionsAndAnimations = Symbol('elementsWithTransitionsAndAnimations');

/**
 * Mixin for mangage transitionend and animationend event
 * @module BbvaCoreTransitionsAndAnimationsEffectMixin
 * @mixinFunction
 */
export const BbvaCoreTransitionsAndAnimationsEffectMixin = dedupingMixin(
  Base =>
    class BbvaCoreTransitionsAndAnimationsEffect extends Base {
      constructor() {
        super();
        this.elementsWithTransitionsAndAnimations = [];
      }

      firstUpdated() {
        super.firstUpdated && super.firstUpdated();

        this.elementsWithTransitionsAndAnimations = this[elementsWithTransitionsAndAnimations];

        ['transitionend', 'animationend'].forEach(type => {
          Array.from(this.elementsWithTransitionsAndAnimations)
            .filter(Boolean)
            .forEach(elementWithTransitionAndAnimation => {
              elementWithTransitionAndAnimation.addEventListener(type, event => {
                // stop event propagation
                event.stopPropagation();
                /**
                 * @event animationsends
                 */
                this.dispatchEvent(
                  new CustomEvent('animationsends', {
                    detail: {
                      originalType: type,
                      originalEvent: event,
                    },
                  }),
                );
              });
            });
        });
      }

      /**
       * Return the elements that use CSS transitions/animations to provide visual effects.
       * By default, this assumes the HOST element itself will have a CSS
       * transition/animation applied to it, and so returns an array containing the element.
       * If you will be applying CSS transitions to other elements, override this
       * property and return an array containing the implicated elements.
       * See [symbols.elementsWithTransitionsAndAnimations](symbols#elementsWithTransitionsAndAnimations)
       * for details.
       * @type {HTMLElement[]}
       */
      get [elementsWithTransitionsAndAnimations]() {
        return [this];
      }
    },
);
