/* global Prism */
/* global markdownit */
import { LitElement, html } from 'lit-element';
import { unsafeHTML } from 'lit-html/directives/unsafe-html';
import { getComponentSharedStyles } from '@bbva-web-components/bbva-core-lit-helpers';
import styles from './BbvaCoreMarkdownElement-styles.js';
import 'prismjs/prism.js';
import 'markdown-it/dist/markdown-it.js';

const MARKDOWNIT = markdownit();

function renderMarkdown(markdown) {
  return html`${unsafeHTML(MARKDOWNIT.render(markdown))}`;
}

function fetchMarkdown(src) {
  return fetch(src)
    .then(response => response.text())
    .catch(error => `Failed to read Markdown source: ${error}`);
}
/**
This component uses `markdown-it` and `Prism JS` to visually show Markdown syntax. It's specially aimed to show component documentation, as it automatically will apply Prism JS styling to code blocks.

The component can receive Markdown directly throught the `markdown` property; fetch a `.md` file passing its path to the `src` property; or parse an inner `<script type="text/markdown">...</script>` tag.

Examples:

```html
<bbva-core-markdown-element markdown="...markdown here..."></bbva-core-markdown-element>
<bbva-core-markdown-element src="path/to/markdown-file.md"></bbva-core-markdown-element>
<bbva-core-markdown-element>
  <script type="text/markdown">
    ...markdown here...
  </script>
</bbva-core-markdown-element>
```

## Styling

The following custom properties are available for styling:

### Custom properties

| Selector             | CSS Property     | CSS Variable                                    | Theme Variable              | Foundations/Fallback                                 |
| -------------------- | ---------------- | ----------------------------------------------- | --------------------------- | ---------------------------------------------------- |
| .markdown-html a     | color            | --__accent-color                                |                             |                                                      |
| .markdown-html p     | color            | --bbva-core-mardown-element-p-color             | --colorsSecondary600        | foundations.colors.secondary600        |
| .markdown-html p     | font-family      | --__font-default                                |                             |                                                      |
| .markdown-html p     | line-height      | --bbva-core-markdown-element-p-line-height      | --lineHeightTypeMedium      | foundations.lineHeight.typeMedium      |
| .markdown-html table | background-color | --bbva-core-markdown-element-table-bg-color     | --colorsSecondary100        | foundations.colors.secondary100        |
| .markdown-html pre   | padding          | --gridSpacerVariant                             | --gridSpacer                | foundations.grid.spacer                |
| .markdown-html code  | border-radius    | --bbva-core-markdown-element-code-border-radius | --borderRadiusSmall         | foundations.borderRadius.small         |
| .markdown-html code  | padding          | --gridSpacerVariant                             | --gridSpacer                | foundations.grid.spacer                |
| .markdown-html code  | background-color | --bbva-core-markdown-element-code-bg-color      | --colorsSecondary100        | foundations.colors.secondary100        |
| .markdown-html pre   | background-color | --bbva-core-markdown-element-code-bg-color      | --colorsSecondary100        | foundations.colors.secondary100        |
| .markdown-html code  | font-size        | --bbva-core-markdown-element-code-font-size     | --typographyTypeSmall       | foundations.typography.typeSmall       |
| .markdown-html pre   | font-size        | --bbva-core-markdown-element-code-font-size     | --typographyTypeSmall       | foundations.typography.typeSmall       |
| .markdown-html h3    | color            | --bbva-core-mardown-element-h3-color            | --colorsSecondary400        | foundations.colors.secondary400        |
| .markdown-html h3    | font-family      | --__font-default                                |                             |                                                      |
| .markdown-html h3    | font-size        | --bbva-core-markdown-element-h3-font-size       | --typographyTypeMedium      | foundations.typography.typeMedium      |
| .markdown-html h2    | color            | --bbva-core-mardown-element-h2-color            | --colorsSecondary500        | foundations.colors.secondary500        |
| .markdown-html h2    | font-family      | --__font-default                                |                             |                                                      |
| .markdown-html h2    | font-size        | --bbva-core-markdown-element-h2-font-size       | --typographyTypeLarge       | foundations.typography.typeLarge       |
| .markdown-html h1    | color            | --bbva-core-mardown-element-h1-color            | --colorsSecondary600        | foundations.colors.secondary600        |
| .markdown-html h1    | font-family      | --__font-default                                |                             |                                                      |
| .markdown-html h1    | font-size        | --bbva-core-markdown-element-h1-font-size       | --typographyTypeXLarge      | foundations.typography.typeXLarge      |
| .markdown-html h1    | line-height      | --bbva-core-markdown-element-h1-line-height     | --lineHeightTypeXLarge      | foundations.lineHeight.typeXLarge      |
| .deeplink:hover      | color            | --__accent-color                                |                             |                                                      |
| .inheritedFrom code  | color            | --__accent-color                                |                             |                                                      |
| :host                | --__accent-color | --bbva-core-markdown-element-accent-color       | --colorsPrimaryCoreLight    | foundations.colors.primaryCoreLight    |
| :host                | --__font-default | --bbva-core-markdown-element-font-default       | --fontFacePrimaryFontFamily | foundations.fontFacePrimary.fontFamily |
> Styling documentation generated by Cells CLI

@customElement bbva-core-markdown-element
@polymer
@LitElement
*/
export class BbvaCoreMarkdownElement extends LitElement {
  static get is() {
    return 'bbva-core-markdown-element';
  }

  static get properties() {
    return {
      /**
       * Markdown code to show
       */
      markdown: {
        type: String,
      },

      /**
       * Path to .md file to show
       */
      src: {
        type: String,
      },

      _scriptTag: {
        type: Object,
        attribute: false,
      },

      _renderedMarkdown: {
        type: String,
        attribute: false,
      },
    };
  }

  constructor() {
    super();
    this.markdown = '';
  }

  firstUpdated() {
    super.firstUpdated && super.firstUpdated();
    this._scriptTag = this.querySelector('script[type="text/markdown"]');
  }

  updated(props) {
    if (props.has('_renderedMarkdown')) {
      Prism.highlightAllUnder(this.shadowRoot, false);
    }
  }

  static get styles() {
    return [styles, getComponentSharedStyles('bbva-core-markdown-element-shared-styles')];
  }

  render() {
    return html` <div class="markdown-html">${this._renderedMarkdown}</div> `;
  }

  set markdown(markdown) {
    this._renderedMarkdown = renderMarkdown(markdown);
  }

  set src(src) {
    if (!src.includes('.md')) {
      this._renderedMarkdown = renderMarkdown('`src` attribute does not specify a MD file');
      return;
    }
    fetchMarkdown(src).then(r => {
      this._renderedMarkdown = renderMarkdown(r);
    });
  }

  set _scriptTag(scriptTag) {
    if (scriptTag) {
      this._renderedMarkdown = renderMarkdown(scriptTag.text.trim());
    }
  }
}
