/* eslint-disable class-methods-use-this */
import { LitElement, html, svg } from 'lit-element';
import { getComponentSharedStyles } from '@bbva-web-components/bbva-core-lit-helpers';
import '@bbva-web-components/bbva-core-collapse/bbva-core-collapse.js';
import '@bbva-web-components/bbva-core-markdown-element/bbva-core-markdown-element.js';
import '@bbva-web-components/bbva-core-heading/bbva-core-heading.js';
import styles from './BbvaCoreComponentDoc-styles.js';

const LITERALS = {
  properties: {
    name: 'properties',
    title: 'Properties',
    single: 'property',
  },
  attributes: {
    name: 'attributes',
    title: 'Attributes',
    single: 'attribute',
  },
  methods: {
    name: 'methods',
    title: 'Methods',
    single: 'method',
  },
  events: {
    name: 'events',
    title: 'Events',
    single: 'event',
  },
};

const dropdownIcon = svg`
<svg viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg">
<path d="M6.555 7.168c-.46-.306-1.08-.182-1.387.277-.306.46-.182 1.08.277 1.387l3 2c.336.224.774.224 1.11 0l3-2c.46-.306.583-.927.277-1.387-.306-.46-.927-.583-1.387-.277L9 8.798l-2.445-1.63z" fill-rule="nonzero" />
</svg>
`;

/**
![LitElement component](https://img.shields.io/badge/litElement-component-blue.svg)

This component shows a component json documentation. It can receive an `elements` array using the `custom-elements.json` or `analysis.json` formats.

Example:

```html
<bbva-core-component-doc></bbva-core-component-doc>
```

```js
const url = '../custom-elements.json';

fetch(url)
  .then( response => response.json() )
  .then(myJson => {
    document.querySelector('bbva-core-component-doc').elements = myJson.elements || myJson.tags;
  })
  .catch(error => {
    console.log(error);
  });
```

@customElement bbva-core-component-doc
@polymer
@LitElement
*/
export class BbvaCoreComponentDoc extends LitElement {
  static get is() {
    return 'bbva-core-component-doc';
  }

  static get properties() {
    return {
      /**
       * Json elements/tags to be shown.
       */
      elements: {
        type: Array,
      },

      /**
       * Strings for documentation blocks headings and literals
       */
      texts: {
        type: Array,
      },

      /**
       * Base heading level for documentation blocks
       */
      headingLevel: {
        type: Number,
        attribute: 'heading-level',
      },
    };
  }

  constructor() {
    super();
    this.elements = [];
    this.texts = LITERALS;
    this.headingLevel = 2;
  }

  get _singleElement() {
    return this.elements && this.elements.length < 2;
  }

  get _noElements() {
    return !this.elements || this.elements.length === 0;
  }

  get _headingLevel() {
    return this.elements && this.elements.length > 1 ? this.headingLevel + 1 : this.headingLevel;
  }

  static get styles() {
    return [styles, getComponentSharedStyles('bbva-core-component-doc-shared-styles')];
  }

  render() {
    return this._noElements
      ? this._renderNoElements
      : html`
          ${this._singleElement
            ? this._renderElement(this.elements[0], 0)
            : this._renderMultipleElements}
        `;
  }

  get _renderNoElements() {
    return html`
      <bbva-core-heading class="empty" .level="${this.headingLevel}"
        >There are no items to show</bbva-core-heading
      >
    `;
  }

  get _renderMultipleElements() {
    return this.elements.map(
      (element, index) => html`
        <div class="element">
          <bbva-core-heading class="title" .level="${this.headingLevel}">
            <button
              class="toggle element-toggle"
              aria-expanded="false"
              aria-controls="element_${index}"
              @click="${e => this._toggleCollapse(e, `element_${index}`)}"
            >
              ${element.tagname || element.name} <span class="icon">${dropdownIcon}</span>
            </button>
          </bbva-core-heading>

          <bbva-core-collapse
            class="collapse element-collapse"
            id="element_${index}"
            no-animation
            tabindex="0"
          >
            ${this._renderElement(element, index)}
          </bbva-core-collapse>
        </div>
      `,
    );
  }

  _renderElement(element, index) {
    return html`
      ${element.description
        ? html`
            <bbva-core-heading class="section" .level="${this._headingLevel}"
              >Description</bbva-core-heading
            >
            <bbva-core-markdown-element
              class="description"
              .markdown="${element.description}"
            ></bbva-core-markdown-element>
          `
        : ''}
      ${this._checkElementDetail(element, 'properties', index)}
      ${this._checkElementDetail(element, 'methods', index)}
      ${this._checkElementDetail(element, 'events', index)}
    `;
  }

  _checkElementDetail(element, detail, index) {
    return element[detail] && element[detail].length
      ? this._renderElementDetails(element[detail], this.texts[detail], index)
      : '';
  }

  _renderElementDetails(items, literals, index) {
    return html`
      <div class="details">
        <bbva-core-heading class="section" .level="${this._headingLevel}"
          >${literals.title}</bbva-core-heading
        >
        <ul>
          ${items
            .filter(m => m.privacy === 'public' || !m.name.startsWith('_'))
            .map(m => this._renderDetail(m, false))}
        </ul>
        ${this._renderElementProtectedDetails(
          items.filter(m => m.privacy === 'protected' || m.name.startsWith('_')),
          literals,
          index,
        )}
      </div>
    `;
  }

  _renderElementProtectedDetails(items, literals, index) {
    const id = `${literals.name}_${index}`;
    const collapse = this.shadowRoot.getElementById(id);
    return html`
      ${items.length
        ? html`
            <bbva-core-collapse
              class="collapse protected-collapse"
              id="${id}"
              no-animation
              tabindex="0"
            >
              <ul>
                ${items.map(m => this._renderDetail(m, true))}
              </ul>
            </bbva-core-collapse>

            <button
              class="toggle protected-toggle"
              aria-expanded="false"
              aria-controls="${id}"
              @click="${e => this._toggleCollapse(e, id)}"
            >
              ${collapse && collapse.opened ? 'Hide' : 'Show'} ${items.length} protected
              ${items.length === 1 ? literals.single : literals.name}
            </button>
          `
        : ''}
    `;
  }

  _renderDetail(item, p) {
    return html`
      <li>
        <bbva-core-heading class="detail" .level="${this._headingLevel + 1}">
          ${p ? html`<code>protected </code>` : ''}
          ${item.name}${item.type ? html`: <span class="type">${item.type}</span>` : ''}
          ${item.default ? html`= <span class="default">${item.default}</span>` : ''}
        </bbva-core-heading>
        ${item.description
          ? html`
              <bbva-core-markdown-element
                .markdown="${item.description}"
              ></bbva-core-markdown-element>
            `
          : ''}
      </li>
    `;
  }

  _toggleCollapse(e, id) {
    const collapse = this.shadowRoot.getElementById(id);
    collapse.opened = !collapse.opened;
    e.target.setAttribute('aria-expanded', collapse.opened);
    this.requestUpdate();
  }
}
