/* eslint-disable no-console */
import { LitElement, html } from 'lit-element';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import * as utils from './utils.js';
import '../bbva-core-demo-case.js';
import '../bbva-core-demo-codemirror.js';
import '../bbva-core-demo-dropdown-menu.js';
import '../bbva-core-demo-event-toaster.js';
import '../bbva-core-demo-resizable-viewport.js';
import '@bbva-web-components/bbva-core-component-doc/bbva-core-component-doc.js';
import '@bbva-web-components/bbva-core-markdown-element/bbva-core-markdown-element.js';
import styles from './BbvaCoreDemoHelper-styles.js';

/**
![LitElement component](https://img.shields.io/badge/litElement-component-blue.svg)

`bbva-core-demo-helper` provides a declarative way of handling demo cases in a demo document for web components. It provides an additional element, `<bbva-core-demo-case>`, which can be used to define each demo case. Each case will be showcased inside an iframe.

`bbva-core-demo-helper` automatically populates a dropdown with all available cases. It also offers multiple additional helpers for the demo, related to resizable viewport, events, ambients, internationalization, polyfills, and code editing.

## Basic usage

`bbva-core-demo-helper` expects one or more `bbva-core-demo-case` instances as direct children. Each `bbva-core-demo-case` must have a `template` tag wrapping all the demo code inside it.

```html
<bbva-core-demo-helper>
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
  <bbva-core-demo-case heading="Case 2">
    <template>
      ...your second demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

A 'Demo case' dropdown will be autopopulated with all available demo cases in HTML. Selecting a demo case will generate an iframe and append the content of the `bbva-core-demo-case` template tag to its body.

__Important:__ the `<head>` tag of the generated iframe will be populated with all available `<script>` tags from the main document `<head>`.

Each demo should have a `heading` attribute with the title of the case, and an optional `description` attribute with an extended text if necessary.

### Descriptions for demo cases

Each demo case must have a 'heading' attribute with the case title, but it also can have an optional 'description' attribute with an extended text describing the case.

```html
<bbva-core-demo-helper>
  <bbva-core-demo-case heading="Case 1" description="This is the description for the first demo case">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

### Separate HTML files as demo cases

It's possible to provide an entire, separate HTML file as content for a demo case. Just provide a 'src' attribute to the demo case including the path to the HTML file.

```html
<bbva-core-demo-helper>
  <bbva-core-demo-case src="./demo-case.html" heading="Case 1" description="This is the description for the first demo case">
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

Please note, anything you include in the `<head>`, as well as any attribute on `<html>` or `<body>` tags of your HTML, will be overwritten when the file is viewed inside `bbva-core-demo-helper` viewport. Make sure any dependency needed for your demo case is declared/exported in `demo/demo.js`.

## Resizable Viewport

`bbva-core-demo-helper` allows to resize the demo iframe through a resizable bars selector. By default, three sizes are provided: 'mobile' (selected by default), 'tablet' and 'desktop'. You can provide a different set of breakpoints using the 'breakpoints' property. This property can receive an object; each key will be a different breakpoint with 'width', 'height' and 'name'.

```html
<bbva-core-demo-helper breakpoints='{
  "mobile": { "width": 320, "height": 480, "name": "Mobile View" },
  "tablet": { "width": 768, "height": 1024, "name": "Tablet View" },
  "desktop": { "width": 1360, "height": 800, "name": "Desktop View" }
}'>
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

You can change the default breakpoint passing one of the breakpoint keys to the 'resolution' property. By default, the available breakpoint keys are 'mobile', 'tablet' and 'desktop'.

```html
<bbva-core-demo-helper resolution="tablet">
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

## Showcasing events

Passing an array of event names to the 'events' properties will set an event listener to the iframe window for each of these event names. When one to the events is dispatched, the listener will launch a toast with the event info on the main document.

```html
<bbva-core-demo-helper events='["event-name-1", "event-name-2"]'>
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

## Internationalization

Passing a 'langs' property containing locales codes will set an `IntlMsg` object to the iframe window according to `BbvaCoreIntlMixin`. It will provide a dropdown with an option for each locale, which will update the `IntlMsg.lang` property in the iframe, thus updating translations in any component using `BbvaCoreIntlMixin` in case there are locales for the provided language.
You can use `locales-host` attribute to specify a global `localesHost` for `BbvaCoreIntlMixin`, defining the path for the locales folder.

```html
<bbva-core-demo-helper langs='["en-US", "es-ES"]' locales-host="..">
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

First lang in the array will be selected by default, but you can change the default one with the 'selectedLang' property.

```html
<bbva-core-demo-helper langs='["en-US", "es-ES"]' selected-lang="es-ES">
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

## Polyfills

It's possible to force the usage of ShadyDOM `webcomponents` polyfills inside the iframe. This way, iframe will be generated using ShadyDOM instead of Shadow DOM, and CSS Custom Properties will be shimmed as they would in browsers with no support for them. Just check the `Shady` checkbox, or pass the `shady` attribute to `bbva-core-demo-helper` to have it applied by default.

```html
<bbva-core-demo-helper shady>
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

## Ambients

`bbva-core-demo-helper` can show a dropdown for changing the applied `ambient` styles to the demo iframe. You can pass an ambients object to the 'ambients' property or, if using Cells CLI, just export it from your `demo/demo.js` file. `bbva-core-demo-helper` will automatically get any export with the `ambient` word in it and pass it to the `ambients` property.

__Important:__ for using ambients with ShadyDOM and not native CSS Variables support, be sure to include the `@bbva-web-components/bbva-core-scoping-ambients-shim` in your demo or application.

__demo.js__
```
import './my-component.js';
export { myComponentAmbient } from './my-component.js';
```

By default, the ambients dropdown will allow to select from 'light' and 'dark' ambient. You can customize the available ambients with the 'availableAmbients' property.

```html
<bbva-core-demo-helper available-ambients='["light", "light100", "dark100", "dark", "dark300"]'>
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

The background colors for the iframe body when an ambient is selected are retrieved from '@bbva-web-components/bbva-foundations-styles' 'ambients' export.

## Code editing

`bbva-core-demo-helper` uses `CodeMirror` for exposing the demo case code in the 'Code' tab. Thus, you can 'live edit' the code of your demo case from that tab. The demo iframe will be regenerated using the new code when you return to the 'Demo' tab. You can also use the 'Copy' button to copy to your clipboard the demo code currently shown in the 'Code' tab.

## Documentation

`bbva-core-demo-helper` can show documentation compliant with [custom-elements-json](https://github.com/webcomponents/custom-elements-json) format in the 'Doc' tab. You can automatically generate a `custom-elements.json` file for your component using [web component analyzer](https://github.com/runem/web-component-analyzer). After installing it, run:

```
$ wca src/*.js --visibility private --outFile custom-elements.json
```

`bbva-core-demo-helper` will look for that file in the root folder of the component, although you can provide a custom path and file name using the `docFile` property.

Packages which do not contain custom elements won't have a `custom-elements.json` file. For those, `mdDocFile` property can be used to pass the path to a markdown file to be shown as documentation.

## Other options

### Default selected case

First available demo case in DOM will be automatically selected on page load. You can show by default a different demo case passing its index order to the 'selected' property. First demo case will have '0' index, second one will be '1', and so on.

```html
<bbva-core-demo-helper selected="1"> <!-- This will select second demo case-->
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
  <bbva-core-demo-case heading="Case 2">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

### Hiding UI

The header with the dropdowns can be hidden using the attribute "hide-ui".

```html
<bbva-core-demo-helper hide-ui>
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

### Filtering `<head>` scripts

Although all `scripts` found in the `head` tag from the main document will be passed down to the iframe `head`, it's possible to filter them. By default, script tag pointing to `demo/demo-build.js` (which contains the code for the demo helper component itself) will be filtered out and not passed down. You can use the 'filterScripts' property to pass an array of scripts to filter out.

```html
<bbva-core-demo-helper filter-scripts='["demo/demo-build.js"]'>
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

### Selected tab

'demo' tab is selected by default. You can change this using the `selectedTab` property to select the 'code' tab.

```html
<bbva-core-demo-helper selected-tab="code">
  <bbva-core-demo-case heading="Case 1">
    <template>
      ...your demo code...
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

@customElement bbva-core-demo-helper
@Polymer
@LitElement
*/
export class BbvaCoreDemoHelper extends LitElement {
  static get is() {
    return 'bbva-core-demo-helper';
  }

  static get properties() {
    return {
      /**
       * Index of currently selected case
       */
      selected: {
        type: Number,
      },

      _currentCase: {
        type: Object,
      },

      /**
       * Array with head scripts to not include in demo case iframe head
       */
      filterScripts: {
        type: Array,
        attribute: 'filter-scripts',
      },

      /**
       * Object containing ambients keys with ambients styling
       */
      ambients: {
        type: Object,
      },

      /**
       * Array of available ambient keys to choose from. Defaults to 'light' and 'dark'
       */
      availableAmbients: {
        type: Array,
        attribute: 'available-ambients',
      },

      /**
       * Currently selected ambient
       */
      selectedAmbient: {
        type: String,
        attribute: 'selected-ambient',
      },

      /**
       * If true, Shady CSS polyfills will be applied on demo case iframe load
       */
      shady: {
        type: Boolean,
      },

      /**
       * Array of events which will show a toast when fired in demo case iframe
       */
      events: {
        type: Array,
      },

      /**
       * Object describing breakpoints for resizable viewport
       */
      breakpoints: {
        type: Object,
      },

      /**
       * Currently selected breakpoint from resizable viewport breakpoints
       */
      resolution: {
        type: String,
      },

      /**
       * Available languages for IntlMixin
       */
      langs: {
        type: Array,
      },

      /**
       * Currently selected language for IntlMixin
       */
      selectedLang: {
        type: String,
        attribute: 'selected-lang',
      },

      /**
       * Path to locales folder for IntlMixin.
       */
      localesHost: {
        type: String,
        attribute: 'locales-host',
      },

      /**
       * Selected tab. Values can be 'demo' or 'code'
       */
      selectedTab: {
        type: String,
        attribute: 'selected-tab',
      },

      /**
       * If true, demo helper header will be hidden on mobile resolution
       */
      hideUi: {
        type: Boolean,
        reflect: true,
        attribute: 'hide-ui',
      },

      /**
       * Path to documentation file to be retrieved
       */
      docFile: {
        type: String,
        attribute: 'doc-file',
      },

      /**
       * Path to legacy documentation file to be retrieved
       */
      legacyDocFile: {
        type: String,
        attribute: 'legacy-doc-file',
      },

      /**
       * Path to markdown documentation file
       */
      mdDocFile: {
        type: String,
        attribute: 'md-doc-file',
      },

      _eventToasterAlignTop: {
        type: Boolean,
        attribute: false,
      },

      _iframeWidth: {
        type: Number,
        attribute: false,
      },

      _iframeHeight: {
        type: Number,
        attribute: false,
      },

      _tabindex: {
        type: Number,
        attribute: false,
      },

      _componentDoc: {
        type: Array,
        attribute: false,
      },

      _componentMd: {
        type: String,
        attribute: false,
      },

      _showLoadError: {
        type: Boolean,
        attribute: false,
      },
    };
  }

  static get styles() {
    return styles;
  }

  constructor() {
    super();
    this.selected = 0;
    this.filterScripts = ['build/demo-build.js'];
    this.availableAmbients = ['light', 'dark'];
    this.selectedAmbient = '';
    this.shady = window.sessionStorage.getItem('bbva-core-demo-helper-shady') === 'true';
    this.resolution = 'mobile';
    this.selectedTab = 'demo';
    this.langs = [];
    this.localesHost = '.';
    this.hideUi = false;
    this._toastAlign = 'bottom';
    this._allDemoCases = [];
    this._currentCase = {};
    this.selectedAmbient = '';
    this._ambientBackgrounds = utils._ambientBackgrounds().join('\n');
    this._noHead = false;
    this._firstRender = true;
    this._cacheScriptsHeadIframe = '';
    this._showLoadError = false;
    this.docFile = '../custom-elements.json';
    this.legacyDocFile = '../analysis.json';

    this._iframeOnWebComponentsReady = this._iframeOnWebComponentsReady.bind(this);
  }

  connectedCallback() {
    super.connectedCallback && super.connectedCallback();
    document.body.style.margin = 0;
    this._allDemoCases = [...this.querySelectorAll(this.constructor.demoCaseTag)];

    if (window !== window.top) {
      this._eventToasterAlignTop = true;
    }
  }

  firstUpdated(changedProps) {
    super.firstUpdated && super.firstUpdated(changedProps);
    this._root = this.shadowRoot || this;
    this._iframeSlot = this._root.querySelector('[name="_iframe"]');
    this._eventToaster = this._root.querySelector('#eventToaster');
    this._editor = this._root.querySelector('#editor');
    this._copyButton = this._root.querySelector('#copyButton');
    this._fetchDocs();
    this._getAmbients();
  }

  static get demoCaseTag() {
    return 'bbva-core-demo-case';
  }

  updated(changedProps) {
    super.updated && super.updated(changedProps);
    if (changedProps.has('_currentCase') && this._currentCase.code) {
      this._reloadIframe();
    }
    if (changedProps.has('selectedTab')) {
      this._updateSelectedTab();
    }
    if (changedProps.has('selected')) {
      this._setCase(this.selected);
    }
    if (changedProps.has('selectedAmbient')) {
      this._setAmbient(this.selectedAmbient);
    }
  }

  set availableAmbients(newValue) {
    const oldValue = this.availableAmbients;
    if (newValue !== oldValue) {
      if (newValue[0].toLowerCase() !== 'none') {
        newValue.unshift('none');
      }
      this._availableAmbients = newValue;
      this.requestUpdate('availableAmbients', oldValue);
    }
  }

  get availableAmbients() {
    return this._availableAmbients;
  }

  _reloadIframe() {
    this._setCaseIframe(this._currentCase.code);
  }

  _returnNewItems(objKey, valueKey, selected) {
    return this[objKey].map((item, index) => ({
      text: item,
      value: valueKey === 'index' ? index : item,
      selected: (objKey === 'langs' ? item : index) === selected,
    }));
  }

  get _shadyHideChecks() {
    return html`
      <div class="header-checks hide-docs">
        <span class="check shady">
          <label for="shady">Shady</label>
          <input
            type="checkbox"
            id="shady"
            ?checked="${this.shady}"
            @change="${this._onShadyChange}"
            tabindex="${ifDefined(this._tabindex)}"
          />
        </span>

        <span class="check ui">
          <label for="hideUi">Hide UI</label>
          <input
            type="checkbox"
            id="hideUi"
            ?checked="${this.hideUi}"
            @change="${e => {
              this.hideUi = e.target.checked;
            }}"
            tabindex="${ifDefined(this._tabindex)}"
          />
        </span>
      </div>
    `;
  }

  get _optionTabs() {
    return html`
      <div class="tabs hide-docs" @change="${this._onTabChange}">
        <input
          type="radio"
          id="demoTab"
          value="demo"
          name="tab"
          ?checked="${this.selectedTab === 'demo'}"
        />
        <label for="demoTab">Demo</label>

        <input
          type="radio"
          id="codeTab"
          value="code"
          name="tab"
          ?checked="${this.selectedTab === 'code'}"
        />
        <label for="codeTab">Code</label>

        <input
          type="radio"
          id="docsTab"
          value="docs"
          name="tab"
          ?checked="${this.selectedTab === 'docs'}"
        />
        <label for="docsTab">Docs</label>
      </div>
    `;
  }

  get _selectors() {
    const casesMenus = this._returnNewItems('_casesMenu', 'index', this.selected);
    const ambients = this._returnNewItems('availableAmbients', 'item', 0);
    const langs = this._returnNewItems('langs', 'item', this._selectedLang);
    return html`
      <div class="selectors hide-docs">
        <bbva-core-demo-dropdown-menu
          class="dropdown"
          label="Demo case"
          @change="${this._onCaseChange}"
          tabindex="${ifDefined(this._tabindex)}"
          .items="${casesMenus}"
        >
        </bbva-core-demo-dropdown-menu>
        ${this.langs && this.langs.length
          ? html`
              <bbva-core-demo-dropdown-menu
                class="dropdown lang"
                label="Language"
                @change="${this._onLanguageChange}"
                tabindex="${ifDefined(this._tabindex)}"
                .items="${langs}"
              >
              </bbva-core-demo-dropdown-menu>
            `
          : ''}
        ${this._ambientsList.length
          ? html`
              <bbva-core-demo-dropdown-menu
                class="dropdown"
                label="Ambient"
                @change="${this._onAmbientChange}"
                tabindex="${ifDefined(this._tabindex)}"
                .items=${ambients}
              >
              </bbva-core-demo-dropdown-menu>
            `
          : ''}
      </div>
    `;
  }

  render() {
    return html`
      <bbva-core-demo-event-toaster
        .events="${this.events}"
        ?align-top="${this._eventToasterAlignTop}"
        id="eventToaster"
      >
      </bbva-core-demo-event-toaster>

      <div class="header">
        <div class="header-content">
          ${this._shadyHideChecks}
          <div class="hide-ui">
            ${this._selectors}
            <div class="heading">
              <div class="description">
                <h1 class="heading-title">${this._currentCase.heading}</h1>
                <p class="heading-description">${this._currentCase.description}</p>
              </div>

              ${this._optionTabs}
            </div>
          </div>
        </div>
      </div>

      <div class="main">
        <div class="code" ?hidden="${this.selectedTab !== 'code'}">
          <div class="code-inner">
            <p class="disclaimer">
              <span class="disclaimer-title">Live code editor</span>
              <span class="disclaimer-text">Modify the code and go back to preview tab</span>
            </p>
            <button
              type="button"
              id="copyButton"
              class="copy-button"
              @click="${this._copyToClipboard}"
            >
              Copy
            </button>
            <div class="code-wrap">
              <bbva-core-demo-codemirror
                theme="material-palenight"
                line-numbers
                id="editor"
                content="${this._currentCase.code}"
              ></bbva-core-demo-codemirror>
            </div>
          </div>
        </div>

        <div class="demo" ?hidden="${this.selectedTab !== 'demo'}">
          <bbva-core-demo-resizable-viewport
            class="resizer hide-ui"
            .breakpoints="${this.breakpoints}"
            .selectedKey="${this.resolution}"
            @breakpoint-change="${this._updateResolution}"
          ></bbva-core-demo-resizable-viewport>

          <div
            id="iframeContainer"
            class="iframe-container"
            style="width:${this._iframeWidth}px; height:${this._iframeHeight}px"
          >
            <slot name="_iframe"></slot>
          </div>
        </div>

        <div class="docs" ?hidden="${this.selectedTab !== 'docs'}">
          <div class="code-inner">
            <p class="disclaimer">
              <span class="disclaimer-title hide-docs">Documentation</span>
            </p>

            ${this._showLoadError
              ? html`
                  <div>
                    <p class="docs-title">custom-elements.json</p>
                    <p class="docs-text">
                      In order to get documentation for web-components you will need to have a
                      custom-elements.json file. <br />
                      Please note that the details of the file are still being discussed so we may
                      adopt to changes in custom-elements.json <br />without a breaking release.
                    </p>
                    <p class="docs-title">How to generate the documentation</p>
                    <p class="docs-text">
                      Generate the component documentation (README.md, custom-elements.json).
                      <strong class="docs-editor"
                        ><span class="user-select">$ </span>cells
                        lit-component:documentation</strong
                      >
                    </p>
                  </div>
                `
              : this._renderDocsDemo}
          </div>
        </div>
      </div>
    `;
  }

  get _casesMenu() {
    return this._allDemoCases.map(item => item.heading);
  }

  _onCaseChange(e) {
    this.selected = e.target.value;
  }

  _onAmbientChange(e) {
    this.selectedAmbient = e.target.value;
  }

  _onLanguageChange(e) {
    this.selectedLang = e.target.value;
    if (this._iframe) {
      const iframeWindow = this._iframe.contentWindow;
      iframeWindow.IntlMsg = iframeWindow.IntlMsg || {};
      iframeWindow.IntlMsg.lang = this.selectedLang;
    }
  }

  get _selectedLang() {
    return this.selectedLang || this.langs[0];
  }

  _onTabChange(e) {
    this.selectedTab = e.target.value;
  }

  _updateSelectedTab() {
    const tab = this.selectedTab;

    if (!this._currentCase.code && tab === 'code') {
      this.requestUpdate('selectedTab');
      return;
    }

    this._tabindex = tab === 'code' ? -1 : undefined;
    if (this._tabindex) {
      this._iframe.setAttribute('tabindex', this._tabindex);
    }

    const editorContent = this._editor.getValue();

    if (tab === 'code') {
      this.updateComplete.then(() => {
        this._editor.refresh();
      });
    }

    if (tab === 'demo' && this._currentCase.code !== editorContent) {
      this._currentCase.code = editorContent;
      this.requestUpdate('_currentCase');
    }
  }

  _updateResolution(e) {
    this._iframeWidth = e.detail.width;
    this._iframeHeight = e.detail.height;
  }

  _getAmbients() {
    if (!this.ambients) {
      this.ambients = utils._getWindowDemoHelperAmbients();
    }
  }

  async _setCase(index) {
    const demoCase = this._allDemoCases[index];
    if (!demoCase) {
      return;
    }
    this.selected = index;
    const code = await this._getCaseCode(demoCase);
    this._currentCase = {
      heading: demoCase.getAttribute('heading'),
      description: demoCase.getAttribute('description'),
      code,
    };
  }

  /* eslint-disable class-methods-use-this */
  async _getCaseCode(demoCase) {
    if (!demoCase.src) {
      return demoCase.getInner();
    }
    const result = await this._getSrcFromFile(demoCase.src);
    return result;
  }

  async _getSrcFromFile(src) {
    const response = await fetch(src);
    if (!response.ok) {
      console.error(`Unable to retrieve demo case source file ${src}: `, response.status);
    }
    const fileCode = await response.text();
    const parser = new DOMParser();
    const doc = parser.parseFromString(fileCode, 'text/html');
    const { body } = doc;
    const scripts = body.querySelectorAll('script');
    scripts.forEach(item => {
      if (item.innerText.indexOf('dev-server') > -1) {
        body.removeChild(item);
      }
    });
    return body.innerHTML;
  }
  /* eslint-enable class-methods-use-this */

  get _iframe() {
    return this._iframeSlot.assignedElements()[0];
  }

  _setCaseIframe(code) {
    this._cleanIframe();
    const iframe = this._generateIframe();
    iframe.setAttribute('slot', '_iframe');

    this.appendChild(iframe);

    // prevent accessing to document not created yet
    /* istanbul ignore else */
    if (!iframe.contentDocument) {
      return;
    }

    this._tpl = this._templateIframe(this._getScriptsHeadIframe(), code);

    iframe.contentDocument.open();
    iframe.contentDocument.write(this._tpl);
    iframe.contentDocument.close();
    this._passEvents(iframe);

    iframe.contentDocument.addEventListener('WebComponentsReady', this._iframeOnWebComponentsReady);
  }

  _iframeOnWebComponentsReady() {
    if (this._firstRender) {
      this._firstRender = false;
      /**
       * Fired when WebComponentsReady event is fired in iframe
       * @event demo-loaded-complete
       */
      this.dispatchEvent(
        new CustomEvent('demo-loaded-complete', {
          bubbles: true,
        }),
      );
    }
  }

  _cleanIframe() {
    const old = this._iframe;
    if (old) {
      this.removeChild(old);
    }
  }

  _generateIframe() {
    const iframe = document.createElement('iframe');

    iframe.addEventListener('load', () => {
      if (!iframe.contentDocument.head.childNodes.length) {
        return;
      }
      /**
       * Fired when iframe load event is set
       * @event iframe-loaded
       */
      this.dispatchEvent(
        new CustomEvent('iframe-loaded', {
          bubbles: true,
        }),
      );
    });

    return iframe;
  }

  _templateIframe(headCode, bodyCode) {
    return `
      <html lang="${window.document.documentElement.lang}">
        <head>
          <meta charset="utf-8">
          <meta name="viewport" content="width=device-width, initial-scale=1">
          <title>Iframe ${window.document.title}</title>
          ${this._headTplShady}
          ${this._headTplIntl}
          ${headCode}
          ${this._headTplAmbients}
        </head>
        <body id="iframeBody" ambient="${this.selectedAmbient}">
          ${bodyCode}
        </body>
      </html>
    `;
  }

  _getScriptsHeadIframe() {
    /* istanbul ignore else */
    if (this._noHead) {
      return '';
    }

    if (this._cacheScriptsHeadIframe) {
      return this._cacheScriptsHeadIframe;
    }

    const head = document.querySelector('head');
    const clone = head.cloneNode(true);
    const scripts = [...clone.querySelectorAll('script')].filter(
      script => !this.filterScripts.some(text => script.outerHTML.includes(text)),
    );

    this._cacheScriptsHeadIframe = scripts
      .map(item => item.outerHTML)
      .join('')
      .replace('&gt;', '>')
      .replace('&lt;', '<')
      .replace('&amp;', '&');

    return this._cacheScriptsHeadIframe;
  }

  get _headTplIntl() {
    return this.langs && this.langs.length
      ? `
      <script>
        window.IntlMsg = window.IntlMsg || {};
        window.IntlMsg.lang = '${this._selectedLang}';
        window.IntlMsg.localesHost = '${this.localesHost}';
      </script>
    `
      : '';
  }

  /**
   * @type {string}
   */
  get _headTplShady() {
    return this.shady
      ? `
      <script>
        if (window.customElements) window.customElements.forcePolyfill = true;
        window.ShadyDOM = { force: true };
        window.ShadyCSS = { shimcssproperties: true };
      </script>
    `
      : `
      <style>
        *:not(:defined) {
          display: none;
        }
      </style>
    `;
  }

  get _ambientsList() {
    return Object.keys(this.ambients || {}).map(ambient => this.ambients[ambient]);
  }

  get _headTplAmbients() {
    return this._ambientsList && this._ambientsList.length
      ? `
      <bbva-core-custom-style>
        <style>
          ${this._ambientBackgrounds}
          ${this._ambientsList.join('\n')}
        </style>
      </bbva-core-custom-style>
    `
      : '';
  }

  _setAmbient(ambient) {
    this.selectedAmbient = ambient;
    if (this._iframe) {
      if (this.shady) {
        this._reloadIframe();
        return;
      }
      this._iframe.contentDocument.body.setAttribute('ambient', ambient);
    }
  }

  _onShadyChange(ev) {
    this.shady = ev.target.checked;
    window.sessionStorage.setItem('bbva-core-demo-helper-shady', this.shady);
    this._reloadIframe();
  }

  _passEvents(iframe) {
    if (this.events) {
      const { events } = this;
      for (let i = 0; i < events.length; i += 1) {
        iframe.contentDocument.documentElement.addEventListener(
          events[i],
          e => {
            this.dispatchEvent(new CustomEvent(events[i], e));
          },
          true,
        );
      }
    }
  }

  _copyToClipboard() {
    const textarea = document.createElement('textarea');
    textarea.style.cssText = 'height:0; overflow:hidden;';
    textarea.value = this._editor.getValue();
    document.body.appendChild(textarea);
    textarea.select();

    try {
      document.execCommand('copy', false);
      this._copyButton.innerText = 'Copied!';
    } catch (err) {
      /* istanbul ignore next */ this._copyButton.innerText = 'Error';
      /* istanbul ignore next */ console.error(err);
    }
    textarea.remove();
    setTimeout(() => {
      this._copyButton.innerText = 'Copy';
    }, 1000);
  }

  _fetchDocs() {
    const url = this.mdDocFile || this.docFile;

    fetch(url)
      .then(response => {
        if (response.ok) {
          return this.mdDocFile ? response.text() : response.json();
        }
        if (!this.mdDocFile) {
          return fetch(this.legacyDocFile).then(responseLegacy => responseLegacy.json());
        }
        throw new Error(`Failed to read Markdown source: ${this.mdDocFile}`);
      })
      .then(r => {
        if (this.mdDocFile) {
          this._componentMd = r;
        } else {
          this._componentDoc = r.elements || r.tags;
        }
      })
      .catch(error => {
        this._showLoadError = true;
        console.log(
          'It is necessary to generate the custom-elements.json file. Failed to load JSON',
          error,
        );
      })
      .finally(() => {
        this.requestUpdate();
      });
  }

  get _renderDocsDemo() {
    const docsDemo = html`
      ${this._componentMd
        ? html`
            <bbva-core-markdown-element
              .markdown="${this._componentMd}"
            ></bbva-core-markdown-element>
          `
        : ''}
      ${this._componentDoc
        ? html`
            <bbva-core-component-doc .elements="${this._componentDoc}"> </bbva-core-component-doc>
          `
        : ''}
    `;

    return docsDemo;
  }
}
