import { dedupingMixin } from '@bbva-web-components/bbva-core-lit-helpers/utils/mixin.js';

// Contains all connected resizables that do not have a parent.
const ORPHANS = new Set();

/**
 * `BbvaCoreResizableMixin` is a mixin that can be used in components to
 * coordinate the flow of resize events between "resizers" (elements that control the
 * size or hidden state of their children) and "resizables" (elements that need to be
 * notified when they are resized or un-hidden by their parents in order to take
 * action on their new measurements).
 *
 * Elements that perform measurement should add the `BbvaCoreResizableMixin` mixin to
 * their element definition and listen for the `bbva-core-resize` event on themselves.
 * This event will be fired when they become showing after having been hidden,
 * when they are resized explicitly by another resizable, or when the window has been
 * resized.
 *
 * Note, the `bbva-core-resize` event is non-bubbling.
 * @polymer
 * @module BbvaCoreResizableMixin
 * @mixinFunction
 */
export const BbvaCoreResizableMixin = dedupingMixin(
  Base =>
    class BbvaCoreResizable extends Base {
      static get properties() {
        return {
          /**
           * The closest ancestor element that implements `BbvaCoreResizableMixin`.
           */
          _parentResizable: {
            type: Object,
            attribute: false,
          },

          /**
           * True if this element is currently notifying its descendant elements of
           * resize.
           */
          _notifyingDescendant: {
            type: Boolean,
            attribute: false,
          },
        };
      }

      constructor() {
        super();

        this._notifyingDescendant = false;

        // We don't really need property effects on these, and also we want them
        // to be created before the `_parentResizable` observer fires:
        this._interestedResizables = [];
        this._boundNotifyResize = this.notifyResize.bind(this);
        this._boundOnDescendantBbvaCoreResize = this._onDescendantBbvaCoreResize.bind(this);

        this.addEventListener(
          'bbva-core-request-resize-notifications',
          this._onBbvaCoreRequestResizeNotifications,
        );
      }

      connectedCallback() {
        super.connectedCallback && super.connectedCallback();

        this.isAttached = true;
        this._requestResizeNotifications();
      }

      disconnectedCallback() {
        super.disconnectedCallback && super.disconnectedCallback();

        this.isAttached = false;

        if (this._parentResizable) {
          this._parentResizable.stopResizeNotificationsFor(this);
        } else {
          ORPHANS.delete(this);
          window.removeEventListener('resize', this._boundNotifyResize);
        }

        this._parentResizable = null;
      }

      updated(changeProps) {
        super.updated && super.updated(changeProps);
        if (changeProps.has('_parentResizable')) {
          this._parentResizableChanged(this._parentResizable);
        }
      }

      /**
       * Can be called to manually notify a resizable and its descendant
       * resizables of a resize change.
       */
      notifyResize() {
        if (!this.isAttached) {
          return;
        }

        this._interestedResizables.forEach(resizable => {
          if (this.resizerShouldNotify(resizable)) {
            this._notifyDescendant(resizable);
          }
        }, this);

        this._fireResize();
      }

      /**
       * Used to assign the closest resizable ancestor to this resizable
       * if the ancestor detects a request for notifications.
       */
      assignParentResizable(parentResizable) {
        if (this._parentResizable) {
          this._parentResizable.stopResizeNotificationsFor(this);
        }

        this._parentResizable = parentResizable;

        if (parentResizable && parentResizable._interestedResizables.indexOf(this) === -1) {
          parentResizable._interestedResizables.push(this);
          parentResizable._subscribeBbvaCoreResize(this);
        }
      }

      /**
       * Used to remove a resizable descendant from the list of descendants
       * that should be notified of a resize change.
       */
      stopResizeNotificationsFor(target) {
        const index = this._interestedResizables.indexOf(target);

        if (index > -1) {
          this._interestedResizables.splice(index, 1);
          this._unsubscribeBbvaCoreResize(target);
        }
      }

      /**
       * Subscribe this element to listen to bbva-core-resize events on the given target.
       *
       * Preferred over target.listen because the property renamer does not
       * understand to rename when the target is not specifically "this"
       *
       * @param {!HTMLElement} target Element to listen to for bbva-core-resize events.
       */
      _subscribeBbvaCoreResize(target) {
        target.addEventListener('bbva-core-resize', this._boundOnDescendantBbvaCoreResize);
      }

      /**
       * Unsubscribe this element from listening to to bbva-core-resize events on the
       * given target.
       *
       * Preferred over target.unlisten because the property renamer does not
       * understand to rename when the target is not specifically "this"
       *
       * @param {!HTMLElement} target Element to listen to for bbva-core-resize events.
       */
      _unsubscribeBbvaCoreResize(target) {
        target.removeEventListener('bbva-core-resize', this._boundOnDescendantBbvaCoreResize);
      }

      /**
       * This method can be overridden to filter nested elements that should or
       * should not be notified by the current element. Return true if an element
       * should be notified, or false if it should not be notified.
       *
       * @param {HTMLElement} element A candidate descendant element that
       * implements `BbvaCoreResizableMixin`.
       * @return {boolean} True if the `element` should be notified of resize.
       */
      // eslint-disable-next-line
      resizerShouldNotify(element) {
        return true;
      }

      _onDescendantBbvaCoreResize(event) {
        if (this._notifyingDescendant) {
          event.stopPropagation();
          return;
        }

        // no need to use this during shadow dom because of event retargeting
        const useShadow = !window.ShadyDOM;
        if (!useShadow) {
          this._fireResize();
        }
      }

      _fireResize() {
        this.dispatchEvent(
          new CustomEvent('bbva-core-resize', {
            bubbles: false,
            composed: true,
            node: this,
          }),
        );
      }

      _onBbvaCoreRequestResizeNotifications(event) {
        const target = /** @type {!EventTarget} */ (event.composedPath()[0]);
        if (target === this) {
          return;
        }

        target.assignParentResizable(this);
        this._notifyDescendant(target);

        event.stopPropagation();
      }

      _parentResizableChanged(parentResizable) {
        if (parentResizable) {
          window.removeEventListener('resize', this._boundNotifyResize);
        }
      }

      _notifyDescendant(descendant) {
        // NOTE(cdata): In IE10, attached is fired on children first, so it's
        // important not to notify them if the parent is not attached yet (or
        // else they will get redundantly notified when the parent attaches).
        if (!this.isAttached) {
          return;
        }

        this._notifyingDescendant = true;
        descendant.notifyResize();
        this._notifyingDescendant = false;
      }

      _requestResizeNotifications() {
        if (!this.isAttached) {
          return;
        }

        if (document.readyState === 'loading') {
          const _requestResizeNotifications = this._requestResizeNotifications.bind(this);

          document.addEventListener('readystatechange', function readystatechanged() {
            document.removeEventListener('readystatechange', readystatechanged);
            _requestResizeNotifications();
          });
        } else {
          this._findParent();

          if (!this._parentResizable) {
            // If this resizable is an orphan, tell other orphans to try to find
            // their parent again, in case it's this resizable.
            ORPHANS.forEach(orphan => {
              if (orphan !== this) {
                orphan._findParent();
              }
            }, this);

            window.addEventListener('resize', this._boundNotifyResize);
            this.notifyResize();
          } else {
            // If this resizable has a parent, tell other child resizables of
            // that parent to try finding their parent again, in case it's this
            // resizable.
            this._parentResizable._interestedResizables.forEach(resizable => {
              if (resizable !== this) {
                resizable._findParent();
              }
            }, this);
          }
        }
      }

      _findParent() {
        this.assignParentResizable(null);

        this.dispatchEvent(
          new CustomEvent('bbva-core-request-resize-notifications', {
            bubbles: true,
            composed: true,
            cancelable: true,
            node: this,
          }),
        );

        if (!this._parentResizable) {
          ORPHANS.add(this);
        } else {
          ORPHANS.delete(this);
        }
      }
    },
);
