import { css, unsafeCSS } from 'lit-element';

export default css`
  :host {
    display: block;
    transition-duration: var(--bbva-core-collapse-transition-duration, 300ms);
    /* Safari 10 needs this property prefixed to correctly apply the custom property */
    -webkit-transition-duration: var(--bbva-core-collapse-transition-duration, 300ms);
    overflow: visible;
    will-change: opacity;
  }

  :host(.closed) {
    display: none;
  }

  :host(:not(.opened)) {
    overflow: hidden;
  }
`;
