import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@bbva-web-components/bbva-core-lit-helpers';
import { BbvaCoreResizableMixin } from '@bbva-web-components/bbva-core-resizable-mixin';
import { BbvaCoreTransitionsAndAnimationsEffectMixin } from '@bbva-web-components/bbva-core-transitions-and-animations-effect-mixin';
import styles from './BbvaCoreCollapse-styles.js';
/**
`bbva-core-collapse` creates a collapsible block of content.  By default, the content will be collapsed.  Use `opened` or `toggle()` to show/hide the content.
```js
    <button @click="toggle">toggle collapse</button>

    <bbva-core-collapse id="collapse">
      <div>Content goes here...</div>
    </bbva-core-collapse>

    ...

    toggle() {
      this.shadowRoot.querySelector('#collapse').toggle();
    }
```
`bbva-core-collapse` adjusts the max-height/max-width of the collapsible element to show/hide the content.  So avoid putting padding/margin/border on the collapsible directly, and instead put a div inside and style that.

```html
    <style>
      .collapse-content {
        padding: 15px;
        border: 1px solid #dedede;
      }
    </style>

    <bbva-core-collapse>
      <div class="collapse-content">
        Content goes here...
      </div>
    </bbva-core-collapse>
```
## Styling

The following custom properties are available for styling:

### Custom properties

| Selector | CSS Property                | CSS Variable                             | Theme Variable | Foundations/Fallback |
| -------- | --------------------------- | ---------------------------------------- | -------------- | -------------------- |
| :host    | transition-duration         | --bbva-core-collapse-transition-duration |                | 300ms                |
| :host    | -webkit-transition-duration | --bbva-core-collapse-transition-duration |                | 300ms                |
> Styling documentation generated by Cells CLI

* @customElement bbva-core-collapse
* @litElement
* @appliesMixin BbvaCoreResizableMixin
*/
export class BbvaCoreCollapse extends BbvaCoreTransitionsAndAnimationsEffectMixin(
  BbvaCoreResizableMixin(LitElement),
) {
  static get is() {
    return 'bbva-core-collapse';
  }

  static get properties() {
    return {
      /**
       * If true, the orientation is horizontal; otherwise is vertical.
       */
      horizontal: {
        type: Boolean,
      },

      /**
       * If true, focus do not move to the current collapse.
       */
      keepFocus: {
        type: Boolean,
        attribute: 'keep-focus',
      },

      /**
       * Set opened to true to show the collapse element and to false to hide it.
       */
      opened: {
        type: Boolean,
      },

      /**
       * Set noAnimation to true to disable animations.
       */
      noAnimation: {
        type: Boolean,
        attribute: 'no-animation',
      },
    };
  }

  constructor() {
    super();
    this.horizontal = false;
    this.opened = false;
    this.keepFocus = false;
    this._desiredSize = '';
    this.addEventListener('animationsends', this._onTransitionEnd.bind(this));
  }

  static get styles() {
    return [styles, getComponentSharedStyles('bbva-core-collapse-shared-styles')];
  }

  connectedCallback() {
    super.connectedCallback && super.connectedCallback();

    this.isAttached = true;

    if (!this.hasAttribute('role')) {
      this.setAttribute('role', 'group');
    }

    if (!this.hasAttribute('aria-hidden')) {
      this.setAttribute('aria-hidden', 'true');
    }
  }

  disconnectedCallback() {
    super.disconnectedCallback && super.disconnectedCallback();
    this.isAttached = false;
  }

  get dimension() {
    return this.horizontal ? 'width' : 'height';
  }

  get _dimensionMax() {
    return this.horizontal ? 'maxWidth' : 'maxHeight';
  }

  get _dimensionMaxCss() {
    return this.horizontal ? 'max-width' : 'max-height';
  }

  /**
   * When true, the element is transitioning its opened state. When false,
   * the element has finished opening/closing.
   */
  get transitioning() {
    return this._transitioning;
  }

  updated(changedProps) {
    super.updated(changedProps);

    if (changedProps.has('horizontal')) {
      this._horizontalChanged();
    }

    if (changedProps.has('opened')) {
      this._openedChanged();
      /**
       * Fired when opened state changes
       * @event opened-changed
       */
      this.dispatchEvent(
        new CustomEvent('opened-changed', {
          bubbles: true,
          composed: true,
          detail: {
            value: this.opened,
          },
        }),
      );
    }
  }

  render() {
    return html` <slot></slot> `;
  }

  _setTransitioning(value) {
    if (value !== this._transitioning) {
      this._transitioning = value;
      /**
       * Fired when transitioning state changes
       * @event transitioning-changed
       */
      this.dispatchEvent(
        new CustomEvent('transitioning-changed', {
          bubbles: true,
          composed: true,
          detail: {
            value: value,
          },
        }),
      );
    }
  }

  /**
   * Toggle the opened state.
   */
  toggle() {
    this.opened = !this.opened;
  }

  show() {
    this.opened = true;
  }

  hide() {
    this.opened = false;
  }

  /**
   * Updates the size of the element.
   * @param {string} size The new value for `maxWidth`/`maxHeight` as css property value, usually `auto` or `0px`.
   * @param {boolean} animated if `true` updates the size with an animation, otherwise without.
   */
  updateSize(size, animated) {
    // Consider 'auto' as '', to take full size.
    size = size === 'auto' ? '' : size;

    let willAnimate =
      animated && !this.noAnimation && this.isAttached && this._desiredSize !== size;

    this._desiredSize = size;

    this._updateTransition(false);

    // If we can animate, must do some prep work.
    if (willAnimate) {
      // Animation will start at the current size.
      const startSize = this._calcSize();

      // For `auto` we must calculate what is the final size for the animation.
      // After the transition is done, _transitionEnd will set the size back to `auto`.
      if (size === '') {
        this.style[this._dimensionMax] = '';
        size = this._calcSize();
      }

      // Go to startSize without animation.
      this.style[this._dimensionMax] = startSize;
      // Force layout to ensure transition will go. Set scrollTop to itself so that compilers won't remove it.
      this.scrollTop = this.scrollTop;
      // Enable animation.
      this._updateTransition(true);
      // If final size is the same as startSize it will not animate.
      willAnimate = size !== startSize;
    }

    // Set the final size.
    this.style[this._dimensionMax] = size;

    this._sendSize(size);

    // If it won't animate, call transitionEnd to set correct classes.
    if (!willAnimate) {
      this._transitionEnd();
    }
  }

  _sendSize(size) {
    /**
     * Fired when target size is known
     * @event send-target-size
     */
    this.dispatchEvent(
      new CustomEvent('send-target-size', {
        bubbles: true,
        detail: {
          size,
        },
      }),
    );
  }

  _updateTransition(enabled) {
    this.style.transitionDuration = enabled && !this.noAnimation ? '' : '0s';
  }

  _horizontalChanged() {
    this.style.transitionProperty = this._dimensionMaxCss;
    const otherDimension = this._dimensionMax === 'maxWidth' ? 'maxHeight' : 'maxWidth';
    this.style[otherDimension] = '';
    this.updateSize(this.opened ? 'auto' : '0px', false);
  }

  _openedChanged() {
    this.setAttribute('aria-hidden', !this.opened);

    this._setTransitioning(true);
    this.classList.toggle('closed', false);
    this.classList.toggle('opened', false);
    this.updateSize(this.opened ? 'auto' : '0px', true);

    // Focus the current collapse.
    if (this.opened && !this.keepFocus) {
      this.focus();
    }
  }

  _transitionEnd() {
    this.style[this._dimensionMax] = this._desiredSize;
    this.classList.toggle('closed', !this.opened);
    this.classList.toggle('opened', this.opened);
    this._updateTransition(false);
    this.notifyResize();
    this._setTransitioning(false);
  }

  _onTransitionEnd(event) {
    if (event.composedPath()[0] === this) {
      this._transitionEnd();
    }
  }

  _calcSize() {
    return this.getBoundingClientRect()[this.dimension] + 'px';
  }
}
