/* eslint-disable no-else-return */
import { css, unsafeCSS } from 'lit-element';
import { grid, typographyCollection } from '@bbva-web-components/bbva-foundations-tokens';

const TOKEN = 'Token';

function shadyStyleDocument(mq) {
  /* istanbul ignore if */
  if (mq.matches) {
    window.ShadyCSS.styleDocument();
  }
}

/* istanbul ignore if  */
if (window.ShadyCSS && window.ShadyCSS.CustomStyleInterface && !window.ShadyCSS.nativeCss) {
  const mqXs = window.matchMedia(`screen and (max-width: ${grid.breakpoints.xs.max / 16}rem)`);
  const mqSm = window.matchMedia(
    `screen and (min-width: ${grid.breakpoints.sm.min / 16}rem) and (max-width: ${
      (grid.breakpoints.md.min - 1) / 16
    }rem)`,
  );
  const mqMd = window.matchMedia(
    `screen and (min-width: ${grid.breakpoints.md.min / 16}rem) and (max-width: ${
      (grid.breakpoints.lg.min - 1) / 16
    }rem)`,
  );
  const mqLg = window.matchMedia(`screen and (min-width: ${grid.breakpoints.lg.min / 16}rem)`);

  // MediaQueryList.addListener() alias for EventTarget.addEventListener(), for backwards compatibility purposes.
  mqXs.addListener(shadyStyleDocument);
  mqSm.addListener(shadyStyleDocument);
  mqMd.addListener(shadyStyleDocument);
  mqLg.addListener(shadyStyleDocument);
}

// - - - - - - - - Private - - - - - - - -  -

function tokenDeclaration(
  { fontStyle, fontWeight, fontSize, lineHeight, fontFamily },
  key,
  environment,
) {
  return `--${key}${environment}: ${fontStyle} ${fontWeight} ${fontSize}/${lineHeight} ${fontFamily};`;
}

function unsafeTypographyCollectionKey(value, collection, key) {
  if (value) {
    return value;
  } else {
    throw new Error(`
      Invalid Token: ${key}
      ===
      Valid Tokens: ${Object.keys(collection)}
    `);
  }
}

function mediaCollectionMap(tokens, typesCollection) {
  return Array.from(tokens)
    .map(key => [
      key,
      Object.keys(unsafeTypographyCollectionKey(typesCollection[key], typesCollection, key)),
    ])
    .reduce((collection, [key, group]) => {
      group.filter(ckey => !collection.has(ckey)).forEach(ckey => collection.set(ckey, new Set()));

      group.map(ckey => collection.get(ckey)).forEach(keys => keys.add(key));
      return collection;
    }, new Map());
}

function mediaRules(mediaCollection, selector, typesCollection, environment) {
  return Array.from(mediaCollection).map(([group, fKeys]) => {
    const tokens = Array.from(fKeys).map(key =>
      Object.keys(typesCollection[key][group]).length !== 0
        ? tokenDeclaration(typesCollection[key][group], key, environment)
        : `--${key}${environment}: inherit;`,
    );
    const breakpointSpec = grid.breakpoints[group];
    const rule = {}.hasOwnProperty.call(breakpointSpec, 'max') ? 'max' : 'min';
    const ruleValue = breakpointSpec[rule] / 16;

    return `@media screen and (${rule}-width: ${ruleValue}rem) { ${selector} { ${tokens.join(
      ' ',
    )} } }`;
  });
}

function buildCssText(typesCollection, tokens, selector, environment) {
  const collectionMap = mediaCollectionMap(tokens, typesCollection);
  const medias = mediaRules(collectionMap.entries(), selector, typesCollection, environment);

  return unsafeCSS(medias.join(' '));
}

// - - - - - - - - Public - - - - - - - -  -

export class FoundationTypography {
  constructor(Collection = typographyCollection) {
    this.tokens = new Map();
    this.Collection = Collection;
  }

  tt(font) {
    if (!this.tokens.has(font)) {
      this.tokens.set(
        font,
        css`var(--${unsafeCSS(font)}, var(--${unsafeCSS(font)}${unsafeCSS(TOKEN)}))`,
      );
    }
    return this.tokens.get(font);
  }

  mediaRules(selector = ':host', environment = TOKEN) {
    return buildCssText(this.Collection, this.tokens.keys(), selector, environment);
  }

  static mediaRulesAll(Collection = typographyCollection, environment = '') {
    return buildCssText(Collection, Object.keys(Collection), ':root', environment);
  }
}
